/** Media Queries **/
.acceso .formulario {
  padding: 1rem;
}
.acceso .formulario .campo {
  display: block;
  margin: 1rem 0 1rem 0;
  justify-content: center;
}
.acceso .formulario .campo label {
  width: 100%;
  justify-content: center;
}
.acceso .formulario .campo input {
  width: 100%;
  margin: 1rem 0 1rem 0;
  padding: 1rem;
}
@media (min-width: 768px) {
  .acceso .formulario .campo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .acceso .formulario .campo label {
    width: 20%;
    justify-content: flex-start;
  }
  .acceso .formulario .campo input {
    width: 80%;
    height: 5rem;
    margin: 0;
    padding: 1rem;
  }
}
.acceso .formulario .mostrarPassword {
  position: relative;
  width: 100%;
}
.acceso .formulario .mostrarPassword .Img {
  position: absolute;
  width: 4rem;
  top: 1.5rem;
  right: 1rem;
  filter: opacity(20%);
}
@media (min-width: 768px) {
  .acceso .formulario .mostrarPassword .Img {
    top: -1.1rem;
  }
}
.acceso .formulario .mostrarPassword .mostrarPass {
  visibility: visible;
}
.acceso .formulario .mostrarPassword .ocultarPass {
  visibility: hidden;
  display: none;
}

