html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #FFFFFF;
  color: rgb(33, 192, 192);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  position: relative;
  min-height: 100vh;
  margin: 0;
  padding-bottom: 10rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.centrado {
  text-align: center;
}

.alinear_derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear_izquierda {
  display: flex;
  justify-content: flex-start;
}

.w-100 {
  width: 100%;
}

.mostrar {
  display: block;
  visibility: visible;
}

.ocultar {
  display: none;
  visibility: hidden;
}

.sombraCaja {
  -webkit-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  -moz-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
}

.grande {
  width: 50rem;
  padding: 1rem;
}

.peque {
  width: 12rem;
  border-radius: 35%;
}

.vermas {
  box-shadow: 1px 1px 5px 1px rgba(0, 1, 14, 0.24);
}

.vermas:active {
  transform: scale(0.9);
  box-shadow: 1px 1px 4px 1px rgba(0, 1, 14, 0.24);
}

.botonCrear:active {
  transform: scale(0.9);
  box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}

.botonCerrar:active {
  transform: scale(0.9);
  box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}

.botonGuardar:active {
  transform: scale(0.9);
  box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}

.swal2-popup.swal2-toast {
  box-sizing: border-box;
  grid-column: 1/4 !important;
  grid-row: 1/4 !important;
  grid-template-columns: min-content auto min-content;
  padding: 1em;
